<style scoped>
.search {
    width: 30%;
}
@media only screen and (max-width: 600px) {
  .search {
    width: 100%;
  }
}

.form__label {
  font-family: 'Roboto', sans-serif;
  font-size: 1.2rem;
  margin-left: 2rem;
  margin-top: 0.7rem;
  display: block;
  transition: all 0.3s;
  transform: translateY(0rem);
}

.form__input {
  font-family: 'Roboto', sans-serif;
  color: #333;
  font-size: 1.2rem;
	margin: 0 auto;
  padding: 1.5rem 2rem;
  border-radius: 0.2rem;
  background-color: rgb(255, 255, 255);
  border: none;
  width: 90%;
  display: block;
  border-bottom: 0.3rem solid transparent;
  transition: all 0.3s;
}

.form__input:placeholder-shown + .form__label {
  opacity: 0;
  visibility: hidden;
  -webkit-transform: translateY(-4rem);
  transform: translateY(-4rem);
}

.mt-10 {
    margin-top: 10px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 19px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 17px;
  width: 17px;
  left: 1px;
  bottom: 1px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #13ce66;
}

input:focus + .slider {
  box-shadow: 0 0 1px #13ce66;
}

input:checked + .slider:before {
  -webkit-transform: translateX(21px);
  -ms-transform: translateX(21px);
  transform: translateX(21px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
</style>
<template>
    <div v-loading.fullscreen.lock="loading">

        <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8" align="right">
            <el-button type="primary" @click="modals.photo = true" size="mini">Зураг байршуулах</el-button>
        </base-header>

        <div class="container-fluid mt--7">
            <div class="row">
                <div class="col">
                    <div class="card shadow border-0 p-5">
                        <!-- <div align="right">
                            <form>
                                <el-input placeholder="Огноо ж. 2021-06-24" v-model="search" clearable class="search"> </el-input>
                            </form>
                        </div> -->
                        <div class="table-responsive mt-2">
                            <table class="table align-items-center table-flush thead-light">
                                <thead>
                                    <th :style="{'background': '#F6F9FC'}">#Дугаар</th>
                                    <th :style="{'background': '#F6F9FC'}">Зураг</th>
                                    <th :style="{'background': '#F6F9FC'}">Эхлэх огноо</th>
                                    <th :style="{'background': '#F6F9FC'}">Дуусах огноо</th>
                                    <th :style="{'background': '#F6F9FC'}">Файлын хэмжээ</th>
                                    <th :style="{'background': '#F6F9FC'}">Тайлбар</th>
                                    <th :style="{'background': '#F6F9FC', 'text-align': 'right'}">Төлөв</th>
                                    <th :style="{'background': '#F6F9FC'}"></th>
                                </thead>
                                <tbody>
                                    <tr v-for="(row, index) in photos" :key="index">
                                        <th scope="row">
                                        <div class="media align-items-center">
                                            <div class="media-body">
                                                {{row.id}}
                                            </div>
                                        </div>
                                    </th>
                                    <th scope="row">
                                        <div class="media align-items-center">
                                            <div class="media-body">
                                                <el-image 
                                                    style="width: 40px; height: auto"
                                                    :src="$appUrl + '/contents/image/slide/'+row.name" 
                                                    :preview-src-list="[$appUrl + '/contents/image/slide/'+row.name]">
                                                </el-image>
                                            </div>
                                        </div>
                                    </th>
                                    <td scope="row">
                                        <div class="media align-items-center">
                                            <div class="media-body">
                                                <span class="name mb-0 text-sm">{{row.created_at}}</span>
                                            </div>
                                        </div>
                                    </td>
                                    <td scope="row" v-if="!row.isChange">
                                        <div class="media align-items-center">
                                            <div class="media-body">
                                                <span class="name mb-0 text-sm">{{row.end_at}}</span> 
                                                <el-tooltip class="item" effect="dark" content="Дуусах хугацааг шинэчлэх" placement="top-end">
                                                    <el-button type="primary" icon="el-icon-edit" size="mini" @click="row.isChange = true" style="margin-left: 10px;" circle></el-button>
                                                </el-tooltip>
                                            </div>
                                        </div>
                                    </td>
                                    <td scope="row" v-else-if="row.isChange">
                                        <div class="media align-items-center">
                                            <div class="media-body">
                                                <el-date-picker
                                                    style="max-width: 150px;"
                                                    v-model="row.changed_at"
                                                    type="date"
                                                    placeholder="Огноогоо сонгоно уу">
                                                </el-date-picker>

                                                <el-button type="success" icon="el-icon-check" size="mini" style="margin-left: 10px;" @click="saveChangedDate(row)" circle></el-button>
                                                <el-button type="danger" icon="el-icon-close" size="mini" @click="hideChangeDate(index)" style="margin-left: 10px;" circle></el-button>
                                            </div>
                                        </div>
                                    </td>
                                    <td scope="row">
                                        <div class="media align-items-center">
                                            <div class="media-body">
                                                <span class="name mb-0 text-sm">{{row.size}} MB</span>
                                            </div>
                                        </div>
                                    </td>
                                    <td scope="row" v-if="!row.descChange">
                                        <div class="media align-items-center">
                                            <div class="media-body">
                                                <span class="name mb-0 text-sm">{{row.description}}</span>
                                                <el-tooltip class="item" effect="dark" content="Тайлбар шинэчлэх" placement="top-end">
                                                    <el-button type="primary" icon="el-icon-edit" size="mini" @click="row.descChange = true" style="margin-left: 10px;" circle></el-button>
                                                </el-tooltip>
                                            </div>
                                        </div>
                                    </td>
                                    <td scope="row" v-else-if="row.descChange">
                                        <div class="media align-items-center">
                                            <div class="media-body">
                                                <el-input placeholder="Тайлбар" style="width: 200px;" maxlength="80" required v-model="row.changed_desc" clearable > </el-input>

                                                <el-button type="success" icon="el-icon-check" size="mini" style="margin-left: 10px;" @click="saveChangedDate(row)" circle></el-button>
                                                <el-button type="danger" icon="el-icon-close" size="mini" @click="hideChangedDesc(index)" style="margin-left: 10px;" circle></el-button>
                                            </div>
                                        </div>
                                    </td>
                                    <td scope="row" align="right">
                                        <div class="media align-items-center">
                                            <div class="media-body">
                                                <!-- {{row.active}} -->
                                                <label class="switch">
                                                    <input type="checkbox" :checked="row.active" @click="changeStat(row.id)">
                                                    <span class="slider round"></span>
                                                </label>
                                            </div>
                                        </div>
                                    </td>
                                    <td align="right">
                                        <el-tooltip class="item" effect="dark" content="Киоск дээр нэмэх" placement="top">
                                            <base-button type="warning" @click="selectPhoto(row.id)" size="sm"><span class="ni ni-cloud-upload-96"></span></base-button>
                                        </el-tooltip>
                                        <el-tooltip class="item" effect="dark" content="Устгах" placement="top">
                                            <base-button type="danger" @click="deletePhoto(row.id)" size="sm"><span class="ni ni-fat-remove"></span></base-button>
                                        </el-tooltip>
                                    </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <!-- <span class="mt-3" @click="getProduct">
                        <base-pagination :page-count="pagination" class="mt-3" v-model="page"></base-pagination>
                    </span> -->
                </div>
            </div>
        </div>

        <modal :show.sync="modals.photo">
            <h6 slot="header" class="modal-title" id="modal-title-default">Зураг байршуулах</h6>
              <div class="row">
                <div class="col-md-12" :style="{'max-height': '400px', 'overflow': 'auto'}">
                    <div class="block">
                        <el-date-picker style="width: 100%;"
                            v-model="newPhoto.created_at"
                            type="date"
                            placeholder="Эхлэх огноо">
                        </el-date-picker>
                    </div>
                    <div class="block mt-10">
                        <el-date-picker style="width: 100%;"
                            v-model="newPhoto.end_at"
                            type="date"
                            placeholder="Дуусах огноо">
                        </el-date-picker>
                    </div>
                    <div class="block mt-10">
                        <el-input placeholder="Тайлбар" maxlength="80" required v-model="newPhoto.description" clearable > </el-input>
                    </div>
                    <div class="block mt-10">
                        <el-button style="width: 100%;" @click="trigger">{{newPhoto.filename}}</el-button>
                    </div>
                    <input type="file" accept="image/*" ref="image" id="image" hidden @change="changeImageUpload" />
                </div>
              </div>

              <template slot="footer">
                <base-button type="link" class="ml-auto" @click="modals.photo = false">Гарах</base-button>
                <base-button type="primary" @click="uploadPhoto">Хадгалах</base-button>
          </template>
        </modal>

        <modal :show.sync="modals.addPhoto">
            <h6 slot="header" class="modal-title" id="modal-title-default">Зураг оруулах</h6>
              <div class="row" style="padding: 10px; max-height: 300px; overflow-y: auto;">
                <!-- <el-input placeholder="Киоскын код" maxlength="20" v-model="addKioskCode" clearable=""> </el-input> -->
                <el-checkbox :indeterminate="kiosks.isIndeterminate" v-model="kiosks.checkall" @change="handleCheckAllChange">Check all</el-checkbox>
                <div style="margin: 15px 0;"></div>
                <el-checkbox-group v-model="kiosks.checked" @change="handleCheckedCitiesChange" style="display: grid; grid-template-columns: 1fr 1fr 1fr;">
                    <el-checkbox v-for="(item, index) in kiosks.list" :label="item.code" :key="index" style="width: 100%;">
                        <span style="margin-left: 30px;">{{item.code}}</span> 
                        <!-- <span style="margin-left: 50px;">{{item.districtname}}, {{item.address}}</span> -->
                    </el-checkbox>
                </el-checkbox-group>
              </div>
              <template slot="footer">
                    <base-button type="link" class="ml-auto" @click="modals.addPhoto = false">Гарах</base-button>
                    <base-button type="primary" @click="savePhoto">Хадгалах</base-button>
            </template>
        </modal>
    </div>
</template>
<script>
  export default {
    data() {
        return {
            photos: [],
            kiosks: {
                checkall: false,
                checked: [],
                list: [],
                isIndeterminate: false
            },
            search: '',
            loading: false,
            modals: {
                photo: false,
                addPhoto: false
            },
            selectedPhoto: '',
            addKioskCode: '',
            newPhoto: {
                description: '',
                SelectedFile: '',
                created_at: '',
                end_at: '',
                filename: 'Зураг байршуулах'                
            },
            changeEdit: false
        }
    },
    mounted() {
      this.getPhotos();
      this.getKiosks();
    },
    methods: {
        handleCheckAllChange(val) {
            if(val) {
                this.kiosks.list.forEach(eleme => {
                    this.kiosks.checked.push(eleme.code);
                });
            } else {
                this.kiosks.checked = [];
            }
            this.kiosks.isIndeterminate = !val;
        },
        handleCheckedCitiesChange(value) {
            let checkedCount = value.length;
            this.kiosks.checkall = checkedCount === this.kiosks.list.length;
            this.kiosks.isIndeterminate = checkedCount > 0 && checkedCount < this.kiosks.list.length;
        },
        getKiosks() {
            var rts = this;
            this.loading = true;
            var token = localStorage.getItem('token');
            rts.$axios({
                method: 'POST',
                url: this.$appUrl + '/user/get-all-kiosk',
                headers: {
                    "Authorization": `Bearer ${token}`
                },
            }).then(data => {
                rts.loading = false;
                if(data.data.result == 'success') {
                    rts.kiosks.list = data.data.kiosk;
                    console.log(data.data.kiosk);
                }
            }).catch(error => {
				rts.loading = false;
				console.log(error);
			});
        },
        formatDate(date) {
            var d = new Date(date),
                month = '' + (d.getMonth() + 1),
                day = '' + d.getDate(),
                year = d.getFullYear();

            if (month.length < 2) 
                month = '0' + month;
            if (day.length < 2) 
                day = '0' + day;

            return [year, month, day].join('-');
        },
        saveChangedDate(row) {
            var rts = this;
            var token = localStorage.getItem('token');
            this.loading = true;
            this.$axios({
                method: 'POST',
                url: this.$appUrl + '/user/update-end-at',
                headers: {
                    "Authorization": `Bearer ${token}`
                },
                data: {
                    row
                }
            }).then(data => {
                rts.loading = false;
                if(data.data.result == 200) {
                    row.end_at = rts.formatDate(row.changed_at);
                    row.description = row.changed_desc;
                    row.active = 1;
                    row.isChange = false;
                    row.descChange = false;
                    rts.$notify({
                        title: 'Амжилттай',
                        message: `Огноо шинэчлэгдлээ`,
                        type: 'success'
                    });
                }
            });
        },
        hideChangeDate(idx) {
            this.photos[idx].changed_at = this.photos[idx].end_at;
            this.photos[idx].isChange = false;
        },
        hideChangedDesc(idx) {
            this.photos[idx].changed_desc = this.photos[idx].description;
            this.photos[idx].descChange = false;
        },
        savePhoto() {
            var rts = this;
            this.loading = true;
            var token = localStorage.getItem('token');
            rts.$axios({
                method: 'POST',
                url: this.$appUrl + '/user/add-kiosk-photo',
                headers: {
                    "Authorization": `Bearer ${token}`
                },
                data: {
                    kiosk: this.kiosks.checked, 
                    id: this.selectedPhoto
                }
            }).then(data => {
                rts.loading = false;
                if(data.data.result == 'success') {
                    rts.$notify({
                        title: 'Амжилттай',
                        message: `Бүртгэгдлээ`,
                        type: 'success'
                    });
                } else if(data.data.result == 'duplicate') {
                    rts.$notify({
                        title: 'Амжилтгүй',
                        message: `Аль хэдийн бүртгэгдсэн`,
                        type: 'danger'
                    });
                } else if(data.data.result == 'failed') {
                    rts.$notify({
                        title: 'Амжилтгүй',
                        message: `Киоск олдсонгүй`,
                        type: 'danger'
                    });
                }
            }).catch(error => {
                rts.loading = false;
                console.log(error);
            });
        },
        selectPhoto(id) {
            this.selectedPhoto = id;
            this.modals.addPhoto = true;
        },
        deletePhoto(id) {
            this.$confirm('Устгахдаа итгэлтэй байна уу?', 'Санамж', {
                confirmButtonText: 'Тийм',
                cancelButtonText: 'Болих',
                type: 'warning'
            }).then(() => {
                var rts = this;
                this.loading = true;
                var token = localStorage.getItem('token');
                rts.$axios({
                    method: 'POST',
                    url: this.$appUrl + '/user/delete-photo',
                    headers: {
                        "Authorization": `Bearer ${token}`
                    },
                    data: {
                        id
                    }
                }).then(data => {
                    rts.loading = false;
                    if(data.data.result == 'success') {
                        this.photos.forEach((el, index) => {
                            if(el.id == id) {
                                rts.photos.splice(index, 1);
                            }
                        });
                    }
                }).catch(error => {
                    rts.loading = false;
                    console.log(error);
                });
            });
        },
        changeStat(id) {
            var rts = this;
            this.loading = true;
            var active = 0, i = 0;
            this.photos.forEach((el, index) => {
                if(el.id == id) {
                    i = index;
                    if(!el.active) {
                        active = 1;
                    }
                }
            });
            var token = localStorage.getItem('token');
            rts.$axios({
                method: 'POST',
                url: this.$appUrl + '/user/update-photo-active',
                headers: {
                    "Authorization": `Bearer ${token}`
                },
                data: {
                    id,
                    active
                }
            }).then(data => {
                rts.loading = false;
                if(data.data.result == 'success') {
                    rts.photos[i].active = active;
                }
            }).catch(error => {
				rts.loading = false;
				console.log(error);
			});
        },
        getPhotos() {
            var rts = this;
            this.loading = true;
            var token = localStorage.getItem('token');
            rts.$axios({
                method: 'POST',
                url: this.$appUrl + '/user/get-photos',
                headers: {
                    "Authorization": `Bearer ${token}`
                }
            }).then(data => {
                rts.loading = false;
                if(data.data.result == 'success') {
                    rts.photos = data.data.photo;
                }
            }).catch(error => {
				rts.loading = false;
				console.log(error);
			});
        },
        uploadPhoto() {
            // console.log(this.newPhoto.SelectedFile);
            var rts = this;
            const token = localStorage.getItem('token');
            if(this.newPhoto.SelectedFile == '' || this.newPhoto.created_at == '' || this.newPhoto.end_at == '' || this.newPhoto.description == '') {
                this.$notify({
                    title: 'Амжилтгүй',
                    message: `Талбараа бүрэн бөглөнө үү`,
                    type: 'danger'
                });
            } else {
                this.loading = true;
                const fd = new FormData();
                fd.append('photo', this.newPhoto.SelectedFile, this.newPhoto.SelectedFile.name);
                fd.append('info', JSON.stringify(this.newPhoto));
                this.$axios.post(this.$appUrl + '/user/photo-upload', fd,
                    {
                        headers: {
                            'Authorization': `Bearer ${token}`
                        }
                    }
                ).then(data => {
                    rts.loading = false;
                    if(data.data.result == 'success') {
                        rts.$notify({
                            title: 'Амжилттай',
                            message: `Зураг бүртгэгдлээ`,
                            type: 'success'
                        });
                        rts.getPhotos();
                    }
                }).catch(error => {
                    rts.loading = false;
                    console.log(error);
                });
            }
        },
        trigger() {
            this.$refs.image.click();
        },
        changeImageUpload(e) {
            this.newPhoto.SelectedFile = e.target.files[0];
            this.newPhoto.filename = this.newPhoto.SelectedFile.name;
        }
    }
  }
</script>
